blockquote {
  padding: 10px 20px;
  border-left: 2px solid #009A61;
  background: #F6F6F6;
  color: #555;
  font-size: 1em;
}

code {
  padding: 2px 4px;
  font-size: 90%;
  color: #c7254e;
  background-color: #f9f2f4;
  border-radius: 4px;
}

[class*=language-] {
  display: block;
  overflow-x: auto;
  padding: .5em;
  color: #abb2bf;
  background: #282c34;
  position: relative;
  border-radius: 6px;
}

[class*=language-]:before {
  content: "";
  position: absolute;
  z-index: 2;
  top: 5px;
  right: 30px;
  border-radius: 6px 0 0 6px;
  background-color: #282c34;
}

[class~=language-js]:before {
  content: "js";
}

[class~=language-ts]:before {
  content: "ts";
}

[class~=language-html]:before {
  content: "html";
}

[class~=language-md]:before {
  content: "md";
}

[class~=language-vue]:before {
  content: "vue";
}

[class~=language-css]:before {
  content: "css";
}

[class~=language-sass]:before {
  content: "sass";
}

[class~=language-scss]:before {
  content: "scss";
}

[class~=language-less]:before {
  content: "less";
}

[class~=language-stylus]:before {
  content: "stylus";
}

[class~=language-go]:before {
  content: "go";
}

[class~=language-java]:before {
  content: "java";
}

[class~=language-c]:before {
  content: "c";
}

[class~=language-sh]:before {
  content: "sh";
}

[class~=language-yaml]:before {
  content: "yaml";
}

[class~=language-javascript]:before {
  content: "js";
}

[class~=language-typescript]:before {
  content: "ts";
}

[class~=language-markup]:before {
  content: "html";
}

[class~=language-markdown]:before {
  content: "md";
}

[class~=language-json]:before {
  content: "json";
}

[class~=language-ruby]:before {
  content: "rb";
}

[class~=language-python]:before {
  content: "py";
}

[class~=language-bash]:before {
  content: "sh";
}

.hljs-comment,
.hljs-quote {
  color: #5c6370;
  font-style: italic;
}

.hljs-doctag,
.hljs-formula,
.hljs-keyword {
  color: #c678dd;
}

.hljs-deletion,
.hljs-name,
.hljs-section,
.hljs-selector-tag,
.hljs-subst {
  color: #e06c75;
}

.hljs-literal,
.hljs-params {
  color: #56b6c2;
}

.hljs-addition,
.hljs-attribute,
.hljs-meta-string,
.hljs-regexp,
.hljs-string {
  color: #98c379;
}

.hljs-built_in,
.hljs-class .hljs-title {
  color: #e6c07b;
}

.hljs-attr,
.hljs-number,
.hljs-selector-attr,
.hljs-selector-class,
.hljs-selector-pseudo,
.hljs-template-variable,
.hljs-type,
.hljs-variable {
  color: #d19a66;
}

.hljs-bullet,
.hljs-link,
.hljs-meta,
.hljs-selector-id,
.hljs-symbol,
.hljs-title {
  color: #61aeee;
}

.hljs-emphasis {
  font-style: italic;
}

.hljs-strong {
  font-weight: bold;
}

.hljs-link {
  text-decoration: underline;
}

.ant-form-item-control {
  line-height: normal !important;
}

.copy-btn {
  height: 18px;
  width: 18px;
  position: absolute;
  right: 4px;
  top: 5px;
  background: url('~assets/images/copy.png') no-repeat;
  background-size: 100% 100%;
  cursor: pointer;
}

.markdown-container {}

.markdown-container {

  code,
  p,
  span {
    font-size: 14px;
  }

  ol>li,
  p,
  ul>li {
    font-size: 16px;
    margin: 10px 0;
  }
}
